Utils.getFriendAlias = function (userInfo) {
  userInfo.alias = userInfo.alias ? userInfo.alias.trim() : ''
  return userInfo.alias || userInfo.nick || userInfo.account
}

Utils.generateChatroomSysMsg = function (data) {
  let text
  switch (data.attach.type) {
    case 'memberEnter':
      text = `${data.attach.fromNick}`
      break
    case 'memberExit':
      text = `${data.attach.fromNick}`
      break
    case 'blackMember':
      text = `${(data.attach.toNick[0] || data.attach.to[0])}`
      break
    case 'unblackMember':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break
    case 'gagMember':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break
    case 'ungagMember':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break
    case 'addManager':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break
    case 'removeManager':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break;
    case 'addTempMute':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break;
    case 'removeTempMute':
      text = `${(data.attach.toNick[0]||data.attach.to[0])}`
      break;
    case 'addCommon':
      text = ``
      break
    case 'removeCommon':
      text = ``
      break
    case 'kickMember':
      text = `${data.attach.toNick[0]}`
      break;
    // case 'xxx':
    // 
    // break;
    default:
      text = ''
      break
  }
  return text
}

Utils.generateTeamSysmMsg = function (data) {
  var text, nicks = this.getNickNames(data.attach.users)
  switch (data.attach.type) {
    case 'updateTeam':
      text = this.getTeamUpdateInfo(data)
      break;
    case 'addTeamMembers': {
      let op = nicks.pop()
      text = `${op}${nicks.join()}`
      break;
    }
    case 'removeTeamMembers': {
      let op = nicks.pop()
      text = `${nicks.join()}${op}`
      break;
    }
    case 'acceptTeamInvite': {
      let op = nicks.pop()
      text = `${nicks.join()}${op}`
      break;
    }
    case 'passTeamApply': {
      let op = nicks.shift()
      if (nicks.length===1 && op===nicks[0]) {
        // 
        text = `${op}`
      } else {
        text = `${op}${nicks}`
      }
      break;
    }
    case 'addTeamManagers': {
      // todo test
      let op = nicks.pop()
      text = `${op}${nicks}`
      break;
    }
    case 'removeTeamManagers': {
      // todo test
      let op = nicks.pop()
      text = `${op}${nicks}`
      break;
    }
    case 'leaveTeam': {
      text = `${nicks.join()}`
      break;
    }
    case 'dismissTeam': {
      text = `${nicks.join()}`
      break;
    }
    case 'transferTeam': {
      // todo test
      let nicks = this.getNickNames(data.attach.users)
      let op = nicks.shift()
      text = `${op}${nicks}`
      break;
    }
    case 'updateTeamMute':{
      let nicks = this.getNickNames(data.attach.users)
      let op = nicks.shift()
      text = `${nicks}${data.attach.mute ? '' : ''}`
      break;
    }
    default:
      break;
  }
  return text
}

// todo 
Utils.getNickNames = function(users) {
  return users.map(user => {
    return user.account === store.state.userUID? '' : user.nick
  })
}

// todo 
Utils.getTeamUpdateInfo = function(msg) {
  let text, team = msg.attach.team, op = this.getNickNames(msg.attach.users).pop()
  if (team['name']) {
    text = `${op}${team['name']}`
  } else if (team['intro']) {
    text = `${op}${team['intro']}`
  } 
  //  Android iOS 
  // else if (team['announcement']) {
  //   text = `${op}${team['announcement']}`
  // } 
  else if (team['joinMode']) {
    text = `${team.joinMode === 'noVerify' ? '' : team.joinMode === 'needVerify' ? '' : ''}`
  } else if (team['inviteMode']) {
    text = `${team['inviteMode'] === 'all' ? '' : ''}`
  } else if (team['updateTeamMode']) {
    text = `${team['updateTeamMode'] === 'all' ? '' : ''}`
  } else if (team['beInviteMode']) {
    text = `${team['beInviteMode'] === 'noVerify' ? '' : ''}`
  } else {
    text = ''
  }
  return text
}

