/*
 * SDK
 */
/*eslint-disable*/

import config from '../../components/im/configs'
import pageUtil from '../../components/im/utils/page'
import util from '../../components/im/utils'
import store from '../store'
import {onFriends, onSyncFriendAction} from './friends'
import {onBlacklist, onMarkInBlacklist} from './blacks'
import {onMyInfo, onUserInfo} from './userInfo'
import {onSessions, onUpdateSession} from './session'
import {onRoamingMsgs, onOfflineMsgs, onMsg} from './msgs'
import {onSysMsgs, onSysMsg, onSysMsgUnread, onCustomSysMsgs} from './sysMsgs'
import {onTeams, onCreateTeam, onTeamMembers, onSyncTeamMembersDone, onUpdateTeamMember, getTeamMembers,getUser,getTeam,createNormalTeam, createAdvancedTeam, acceptTeamInvite,rejectTeamInvite} from './teams'
import cookie from '../../components/im/utils/cookie'
// const ulrs = config.sdk
// const SDK = require('@/sdk/' + ulrs)
export function initNimSDK({state, commit, dispatch}, loginInfo) {
  if (state.niym) {
    state.nim.disconnect()
  }
  dispatch('showLoading')
  // SDK
  // console.log(state.nim)
  window.nim = state.nim = SDK.NIM.getInstance({
    // debug: true && { api: 'info', style: 'font-size:12px;color:blue;background-color:rgba(0,0,0,0.1)' },
    appKey: config.appkey,
    account: loginInfo.uid,
    syncSessionUnread:true,
    token: loginInfo.sdktoken,
    db: false,
    onconnect: function onConnect(event) {
      if (loginInfo) {
        // uid
        commit('updateUserUID', loginInfo)
      }
    },
    onerror: function onError(event) {
      // alert(JSON.stringify(event))
     
      if (cookie.readCookie('uid')!==null) {
        let loginInfo = {
          uid: cookie.readCookie('uid'),
          sdktoken: cookie.readCookie('sdktoken'),
        }
        store.dispatch('initNimSDK', loginInfo)
      }else{
        alert('')
        location.href = config.loginUrl
      }
    
    },
    onwillreconnect: function onWillReconnect() {
      console.log(event)
    },
    ondisconnect: function onDisconnect(error) {
      switch (error.code) {
        // , 
        case 302:
          pageUtil.turnPage('', 'login')
          break;
        // , 
        case 'kicked':
          let map = {
            PC: '',
            Web: '',
            Android: '',
            iOS: '',
            WindowsPhone: ''
          }
          let str = error.from
          let errorMsg = `${util.formatDate(new Date())}${(map[str] || '')}!`
          pageUtil.turnPage(errorMsg, 'login')
          break
        default:
          break
      }
    },
    // // 
    // onloginportschange: onLoginPortsChange,
    // 
    onblacklist: onBlacklist,
    onsyncmarkinblacklist: onMarkInBlacklist,
    // onmutelist: onMutelist,
    // onsyncmarkinmutelist: onMarkInMutelist,
    onfriends: onFriends,
    onsyncfriendaction: onSyncFriendAction,
    //  - actions/userInfo
    onmyinfo: onMyInfo,
    onupdatemyinfo: onMyInfo,
    onusers: onUserInfo,
    onupdateuser: onUserInfo,
    // // 
    onteams: onTeams,
    onsynccreateteam: onCreateTeam,
    onteammembers: onTeamMembers,
    onsyncteammembersdone: onSyncTeamMembersDone,
    onupdateteammember: onUpdateTeamMember,
    getTeamMembers: getTeamMembers,
    getUser: getUser,
    getTeam: getTeam,
    createNormalTeam: createNormalTeam,
    createAdvancedTeam: createAdvancedTeam,
    acceptTeamInvite: acceptTeamInvite,
    rejectTeamInvite: rejectTeamInvite,
    // // 
    onsessions: onSessions,
    onupdatesession: onUpdateSession,
    // // 
    onroamingmsgs: onRoamingMsgs,
    onofflinemsgs: onOfflineMsgs,
    onmsg: onMsg,
    // // 
    onsysmsg: onSysMsg,
    onofflinesysmsgs: onSysMsgs,
    onupdatesysmsg: onSysMsg, // 

    onsysmsgunread: onSysMsgUnread,
    onupdatesysmsgunread: onSysMsgUnread,

    onofflinecustomsysmsgs: onCustomSysMsgs,
    oncustomsysmsg: onCustomSysMsgs,
    // // 
    onsyncdone: function onSyncDone() {
      dispatch('hideLoading')
      // 
      if (store.state.currSessionId) {
        dispatch('setCurrSession', store.state.currSessionId)
      }
    }
  })
}
